export default{
	data(){
		return{
			isInput:false,//是否输入账号,
			checkTel:false,
			designer:{
				mobile:'',
				vcode:''
			}
		}
	},
	components:{

	},
	onLoad(){

	},
	methods:{
		//重置密码
		toResetPassword(){
			if(this.checkTel){
				uni.navigateTo({
					url:'../reset-password/reset-password'
				})
			}else{
				uni.showToast({
					title:'号码不存在',
					icon:'none'
				})
				return;
			}
			
		},
		checkTelEvent(event){
			if(event){
				if((/^1[34578]\d{9}$/.test(event))){ 
					this.checkTel=true;
		
				} 
			}
		},
	}
}
